}

.progress {
  position: relative;
  width: 100%;
  height: 30px;
  background-color: #e2e2e2;
  border-radius: 40px;
}

.progress .bar {
  position: absolute;
  border-radius: 40px;
  height: 100%;
  width: 0%;
  background-color: #304ffe;
  transition: width 0.5s;
}

.progress .counter {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-weight: bold;
}

.success-container {
  margin-top: 10px;
}

.success {
  color: green;
