/* Reset some default styles */
body, h1, h2, h3, h4, h5, h6, p, ul, li, a {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Global body styles */
body {
  font-family: Arial, sans-serif;
  background-color: #f0f2f5;
  color: #333;
  line-height: 1.6;
}

/* Main app container */
.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Navbar styles */
.navbar {
  background-color: white;
  color: #fff;
  padding: 1rem;
}

/* Main content area */
.main-content {
  display: flex;
  flex: 1 1;
  padding: 1rem;
}

.main-content.with-sidebar {
  margin-left: 250px; /* Adjust based on sidebar width */
}

/* Forms */
form {
  max-width: 400px;
  margin: 2rem auto;
  padding: 1rem;
  background: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

form div {
  margin-bottom: 1rem;
}

form label {
  display: block;
  margin-bottom: 0.5rem;
}

form input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

form button {
  background-color: #28a745;
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

form button:hover {
  background-color: #218838;
}

/* Headers */
h2 {
  margin-bottom: 1rem;
  text-align: center;
}

/* Container styles */
.container {
  padding: 1rem;
  max-width: 1200px;
  margin: 0 auto;
}

